import React from "react";
import Pure from "./Pure";
import CenterImage from "../images/CenterHD.webp";
import client from "./CLASSES/client";
import Building from "./CLASSES/buildings";
import "./Center.css";

class Center extends Pure {
    ready = false;
    state = { Content: [] }
    onReady() {
        client.village(village => {
            if (!village) return;
            let Build = village.buildings, vId = village.id;
            let imageArray = [];
            let linksArray = [Building.FromResponse(Build[39], vId).LinkComponent];
            for (let i = 18; i < 39; i++) {
                let B = Building.FromResponse(Build[i], vId);
                let R = B.buildingId === 16; // Is Rally point
                if (!R || (R && B.level > 0))
                    imageArray.push(B.ImageComponent);
                linksArray.push(B.LinkComponent);
            }
            this.ready = true;
            this.updateState({ Content: imageArray.concat(linksArray) });
        });
    }
    render() {
        if (this.ready)
            return <div id="village_center">
                <h4 className="title" ref={this.title}>{client.activeVillageName}</h4>
                <svg viewBox="0 0 540 448">
                    <image className="bg-center" xlinkHref={CenterImage} x="0" y="0" height="540" width="448" />
                    {this.state.Content}
                </svg>
            </div>;
        else
            return <></>;
    }
}

export default Center;